* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
}

body {
    /* background: #454545; */
    color: #ffffff;
}

.container{
    width: 80%;
    padding: 0 1rem;
    margin: 0 auto;
}

button {
    font-size: 15px;
    margin:5px
}

section {
    /* width: 90vw; */
    /* height: 90vh; */
    margin: 20px 0 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.card{
    /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset; */
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    color: black;
    width: 300px;
    height: 150px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
}

.card-holders {
    display: flex;
    flex-wrap: wrap;
}

.card h1 {
    padding-top: 10px;
}

.action-btns {
    flex : 1;
    display: flex;
    justify-content: center;
    align-items: center;
}